import sidebarReducer from "./sidebar-reducer";

const SEND_MESSAGE = "SEND_MESSAGE";

let initialState = {
    dialogs: [
        {
            id: 1,
            name: 'Dima',
            avatarImage: 'data:image/png;base64,'
        },
        {
            id: 2,
            name: 'Vasya',
            avatarImage: 'data:image/jpeg;base64,'
        },
        {
            id: 3,
            name: 'Kolya',
            avatarImage: 'data:image/jpeg;base64,'
        },
        {
            id: 4,
            name: 'Olya',
            avatarImage: 'data:image/jpeg;base64,'
        }
    ],
    messages: [
        {id: 1, message: "Hi", isMyMessage: true},
        {id: 2, message: "How are you?", isMyMessage: false},
        {id: 3, message: "Not bad", isMyMessage: true},
    ],
};

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEND_MESSAGE:
            let body = action.newMessageBody;
            return {
                ...state,
                messages: [...state.messages, {id: 6, message: body}]
            };
        default:
            return state;
    }
}

export const sendMessageClick = (newMessageBody) => {
    return {
        type: SEND_MESSAGE,
        newMessageBody
    };
}

export default dialogsReducer;